body {
  padding-top: 60px;
}

.navbar {
  padding-left: 0.3rem;
  padding-right: 0.3rem;
}
.Logo {
  max-height: 45px;
}

.userbar {
  font-size: 0.8rem;
  color: black;
  background-color: #eeeeee;
  margin-top: 0.6rem;
}

.app-footer {
  background-color: white !important;
}

.base-shape {
  opacity: "0";
}

.showCardBody {
  display: block;
}

.hideCardBody {
  display: none;
}

.thumb-bg {
  background-repeat: no-repeat;
  background-color: #ffc107;
}

@media (max-width: 991.98px) {
  .userbar {
    width: 100%;
  }
}
